  font-weight: normal;
  font-size: 16px;
  line-height: 1.25;
  display: flex;
  align-items: center;
  text-align: center;
  color: #FFFFFF;
  padding-top: 186px;
  width: 161px;
  justify-content: center;
  position: relative;
  left: 60px;
}
@media (min-width: 768px) {
  .additional__gift {
    padding-top: 78px;
    text-align: left;
    left: 424px;
  }
}
@media (min-width: 1440px) {
  .additional__gift {
    text-align: center;
    left: 32px;
    top: 88px;
  }
}
.additional__item-gift--icon {
  position: relative;
}
.additional__item-gift--icon::before {
  content: "";
  position: absolute;
  top: 23%;
  left: 35%;
  width: 78px;
  height: 80px;
  background-image: url("../../img/icons/icon_gift.svg");
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .additional__item-gift--icon::before {
    top: 29%;
    left: 12%;
  }
}
@media (min-width: 1440px) {
  .additional__item-gift--icon::before {
    left: 33%;
  }
}
.additional__wrapper {
  padding: 0 20px;
}
@media (min-width: 768px) {
  .additional__wrapper {
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .additional__wrapper {
    margin: auto;
    max-width: 1220px;
  }
}
@media (min-width: 1440px) {
  .additional__container {
    display: grid;
    grid-template-columns: max-content min-content;
    column-gap: 79px;
    margin-bottom: 86px;
  }
}
.additional__data-1 {
  padding-right: 112px;
}
.additional__data-2 {
  padding-right: 188px;
}
.additional__data-3 {
  padding-right: 199px;
}
.additional__data-4 {
  padding-right: 124px;
}
.page-header {
  padding-top: 12px;
}
@media (min-width: 768px) {
  .page-header {
    padding-top: 42px;
  }
}
.page-header__logo {
  height: 55px;
  display: block;
}
@media (min-width: 768px) {
  .page-header__logo {
    height: 52px;
    margin-right: 49px;
  }
}
@media (min-width: 1440px) {
  .page-header__logo {
    height: 59px;
  }
}
@media (min-width: 768px) {
  .page-header__logo-image:hover {
    opacity: 0.8;
  }
  .page-header__logo-image:active {
    opacity: 0.6;
  }
}
.decor-line {
  position: relative;
}
.decor-line::after {
  position: absolute;
  content: "";
  top: 14px;
  left: 0;
  height: 2px;
  background-color: #68B738;
  z-index: -1;
  overflow: hidden;
  margin-left: calc(50% - 50vw);
  margin-right: calc(50% - 50vw);
  width: 100vw;
}
@media (min-width: 768px) {
  .decor-line::after {
    left: -20px;
  }
}
@media (min-width: 1440px) {
  .decor-line::after {
    left: 0;
  }
}
