.questionnaire__form {
  border-top: 1px solid @special_grey-8;

  @media (min-width: @tablet-width) {
    border: none;
  }
}

.questionnaire__title {
  color: @basic_black;
  font-weight: normal;
  font-size: 36px;
  line-height: 1;
  margin-top: 27px;
  margin-bottom: 37px;

  @media (min-width: @tablet-width) {
    font-size: 60px;
    margin-top: 65px;
    margin-bottom: 54px;
  }
}

.questionnaire__description {
  font-weight: normal;
  font-size: 14px;
  line-height: 1.29;
  text-transform: uppercase;
  color: @basic_black;
  margin: 0;
  margin-bottom: 39px;

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 1.5;
    margin-bottom: 66px;
  }
}

.questionnaire__wrapper {
  padding: 0 20px;

  @media (min-width: @tablet-width) {
    padding: 0 30px;
  }

  @media (min-width: @desktop-width) {
    margin: auto;
    max-width: 1220px;
  }
}

.questionnaire__fields-list {
  .list-reset();

  border-top: 1px solid @special_grey-8;
  border-bottom: 1px solid @special_grey-8;
  margin: 0 -20px;

  @media (min-width: @tablet-width) {
    border: 1px solid @special_grey-8;
    margin: 0;
    margin-top: 52px;
  }

  @media (min-width: @desktop-width) {
    margin-top: 0;
  }
}

.questionnaire__container-main {
  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 80px;
  }
}

.questionnaire__fields-item {
  margin-top: 23px;
  margin-bottom: 19px;
  margin-left: 55px;
}
